/* global QUnit, zenLi, eachElement, tinysort */

const {module,test,ok} = QUnit
  ,aList = ['eek-','oif-','myr-','aar-','oac-','eax-']
  ,sJoin = aList.slice(0).sort().join('')

module('regression')
test('issue 8', ()=>{
  ok((()=>{
    const aSorted = tinysort(zenLi('ul>li{a$}*13',{a:['Q','R','S','T','U','V','W','X','Y','Z','','','']}))
      ,sSorted = eachElement(aSorted)
    return sSorted==='QRSTUVWXYZ'
  })(),'fixed using new')
})
test('issue 10', ()=>{
  ok((()=>{
    const aNodes = zenLi('ul>li#a${a}*6',{a:aList})
    aNodes[2].removeAttribute('id')
    const aSorted = tinysort(aNodes)
    const sSorted = eachElement(aSorted,function(elm){return elm.getAttribute('id')||''})
    return sSorted==='eek-oif-aar-oac-eax-'
  })())
})
test('issue 13', ()=>{
  ok((()=>{
    const aSorted = tinysort(zenLi('ul>li{a$}*6',{a:['eEk-','oif-','myr-','aar-','oac-','eax-']}))
      ,sSorted = eachElement(aSorted)
    return sSorted==='aar-eax-eEk-myr-oac-oif-'
  })(),'regular order')
  ok((()=>{
    const aSorted = tinysort(zenLi('ul>li{a$}*6',{a:['eEk-','oif-','myr-','aar-','oac-','eax-']}),{cases:true})
      ,sSorted = eachElement(aSorted)
    return sSorted==='aar-eEk-eax-myr-oac-oif-'
  })(),'case sensitive order')
  ok((()=>{
    const aSorted = tinysort(zenLi('ul>li{a$}*6',{a:aList}),{sortFunction:function(a,b){
        const aa = a[0], bb = b[0]
        return aa==bb?0:(aa>bb?1:-1)
      }})
      ,sSorted = eachElement(aSorted)
    return sSorted==='eek-oif-myr-aar-oac-eax-'
  })(),'custom sort function')
})
test('issue 14', ()=>{
  ok((()=>{
    const aSorted = tinysort(zenLi('ul>li[data-foo=a$]{_a$}*6',{a:aList}),{data:'foo'})
      ,sSorted = eachElement(aSorted,function(elm){return elm.getAttribute('data-foo')})
    return sSorted===sJoin
  })(),'implement data-attribute support')
})
test('issue 15', ()=>{
  ok((()=>{
    const aSorted = tinysort(zenLi('ul>li{a$}*5',{a:['01','001','a','0a','ba']}))
      ,sSorted = eachElement(aSorted)
    return sSorted==='010010aaba'
  })(),'implementation of forceStrings')
})
test('issue 24', ()=>{
  ok((()=>{
    const aSorted = tinysort(zenLi('ul>li{a$}*5',{a:[20,0,-30,40,-120]}))
      ,sSorted = eachElement(aSorted)
    return sSorted==='-120-3002040'
  })(),'negative numeral value bug')
})
test('issue 27', ()=>{
  ok((()=>{
    const aSorted = tinysort(zenLi('ul>li[data-foo=a$]{_a$}*8',{a:[20,0,-30,20.5,'a','a01',40,-120]}))
      ,sSorted = eachElement(aSorted)
    return sSorted==='_-120_-30_0_20_20.5_40_a_a01'
  })(),'data integer bug')
})
test('issue 39', ()=>{
  ok((()=>{
    const aSorted = tinysort(zenLi('ul>li[value=a$]{_a$}*6',{a:[0,5,1,4,2,3]}),{attr:'value'})
      ,sSorted = eachElement(aSorted)
    return sSorted==='_0_1_2_3_4_5'
  })(),'regexp match onto number bug')
})
test('issue 41', ()=>{
  ok((()=>{
    const aList = ['rythre','gypte','espagne','estonie','mirats arabes unis','quateur','tats-unis','thiopie']
      ,aSorted = tinysort(zenLi('ul>li{_a$}*'+aList.length,{a:aList}),{charOrder:'a[]c[]e[]i[]o[]u[]'})
      ,sSorted = eachElement(aSorted)
    return sSorted==='_gypte_mirats arabes unis_quateur_rythre_espagne_estonie_tats-unis_thiopie'
  })(),'French')
})
