test('issue 51', ()=>{
  ok((()=>{
    const aSorted = tinysort(zenLi('ul>li{_a$}*6',{a:[' 0 ',' 5 ',' 1 ',' 4 ',' 2 ',' 3 ']}))
      ,sSorted = eachElement(aSorted)
    return sSorted==='_ 0 _ 1 _ 2 _ 3 _ 4 _ 5 '
  })(),'numeral values with leading and trailing spaces')
})
test('issue 62', ()=>{
  ok((()=>{
    const aList = ['rythre','gypte',2345,'estonie',9876,'quateur','tats-unis','thiopie']
      ,aSorted = tinysort(zenLi('ul>li{_a$}*'+aList.length,{a:aList}),{charOrder:'a[]c[]e[]i[]o[]u[]'})
      ,sSorted = eachElement(aSorted)
    return sSorted==='_gypte_quateur_rythre_estonie_tats-unis_thiopie_2345_9876'
  })(),'French')
})
test('issue 76', ()=>{
  ok((()=>{
    const aSorted = tinysort(zenLi('ul>li[data-foo=$]*9'))
      ,sSorted = eachElement(aSorted,function(elm){return '_'+elm.getAttribute('data-foo')})
    return sSorted==='_1_2_3_4_5_6_7_8_9'
  })(),'keep original order for same order values (9)')
  ok((()=>{
    const aSorted = tinysort(zenLi('ul>li[data-foo=$]*11'))
      ,sSorted = eachElement(aSorted,function(elm){return '_'+elm.getAttribute('data-foo')})
    return sSorted==='_1_2_3_4_5_6_7_8_9_10_11'
  })(),'keep original order for same order values (11)')
})
test('issue 77', ()=>{
  ok((()=>{
    const aLi = zenLi('ul>li{a $}*6')
    aLi[3].innerHTML = 'a\n4'
    aLi[1].innerHTML = 'a  2'
    const aSorted = tinysort(aLi)
    const sSorted = eachElement(aSorted)
    return sSorted==='a 1a  2a 3a\n4a 5a 6'
  })(),'newlines and multiple spaces')
})
test('issue 78', ()=>{
  ok((()=>{
    const aTest = (function(a,i){
        while (i--) {
          a.push(i)
        }
        return a.sort(function(){return Math.random()<0.5?1:-1})
      })([],14)
      ,aSorted = tinysort(zenLi('ul>li[data-position=a$]{a$}*'+aTest.length,{a:aTest}))
      ,sSorted = eachElement(aSorted)
    return sSorted==='012345678910111213'
  })(),'mixed literal and numeral')
})
test('issue 113', ()=>{
  ok((()=>{
    const fragment = document.createDocumentFragment()
    let i = 6
    while (i--) {
      const li = document.createElement('li')
      li.innerText = i
      fragment.appendChild(li)
    }
    const aSorted = tinysort(fragment.querySelectorAll('li'))
      ,sSorted = eachElement(aSorted)
    return sSorted==='012345'
  })(),'empty selector')
})
test('issue 114', ()=>{
  ok((()=>{
    const aSorted = tinysort(zenLi('ul>li.test{_a$}*6',{a:[' 0 ',' 5 ',' 1 ',' 4 ',' 2 ',' 3 ']}),'.test-none')
      ,sSorted = eachElement(aSorted)
    return sSorted===''
  })(),'empty selector')
})
