test('issue 116', ()=>{
  ok((()=>{
    const ul = document.createElement('ul')
    ul.innerHTML = `
      <li><span>1</span></li>
      <li><span data-mod="c">3</span>c</li>
      <li><span>5</span></li>
      <li><span>7</span></li>
      <li><span data-mod="b">0</span>b</li>
      <li><span>4</span></li>
      <li><span data-mod="a">9</span>a</li>`
    const aSorted = tinysort(ul.querySelectorAll('li'),{
        selector: 'span'
        ,data: 'mod'
      },{})
      ,sSorted = eachElement(aSorted)
    console.log('sSorted',sSorted) // todo: remove log
    return sSorted==='9a0b3c1457'
  })(),'empty selector')
})

