test('issue 139', ()=>{

  const getLiFrom = listring=>{
    const ul = document.createElement('ul')
    ul.innerHTML = listring
    return ul.querySelectorAll('li')
  }

  /*ok((()=>{
    const aSorted = tinysort(zenLi('ul>li{a$}*4',{a:'ab'.split('')}),{charOrder:'a'})
      ,sSorted = aSorted.map(elm=>elm.textContent).join('')
    return sSorted=='ab'
  })(),'normal charorder')*/

  ok((()=>{
    const aSorted = tinysort(getLiFrom('<li>c</li><li foo="a">a</li><li foo="">b</li><li foo>e</li>'),{attr:'foo',emptyEnd:true})
      ,sSorted = aSorted.map(elm=>elm.textContent).join('')
    return sSorted=='acbe'
  })(),'normal attribute order')

  ok((()=>{
    const aSorted = tinysort(getLiFrom('<li>c</li><li foo="a">a</li><li foo="">b</li><li foo>e</li>'),{attr:'foo',emptyEnd:false})
      ,sSorted = aSorted.map(elm=>elm.textContent).join('')
    return sSorted=='cbea'
  })(),'normal attribute order empty end')

  ok((()=>{
    const aSorted = tinysort(getLiFrom('<li>c</li><li foo>a</li><li>b</li><li>e</li>'),{attr:'foo',emptyEnd:true})
      ,sSorted = aSorted.map(elm=>elm.textContent).join('')
    console.log('sSorted',sSorted) // todo: remove log
    return sSorted=='acbe'
  })(),'normal attribute order')

  ok((()=>{
    const aSorted = tinysort(getLiFrom('<li>c</li><li foo>a</li><li>b</li><li>e</li>'),{attr:'foo',emptyEnd:false})
      ,sSorted = aSorted.map(elm=>elm.textContent).join('')
    console.log('sSorted',sSorted) // todo: remove log
    return sSorted=='cbea'
  })(),'normal attribute order empty end')

})



