/* global QUnit, zenLi, eachElement, tinysort */

// import {loadScript} from '../doc/js/util'

const {module,test,ok,/*assert,async*/} = QUnit
  ,aLangSR = ['dep','luak','ovjek','gospodin','mukarac','ljubav','coga','zec','ega','liljana','godina','no','njuka']
  ,aLangDA = ['ben','ble','se','Kbenhavn','Aarhus','resund']
  ,aLangFR = ['sdf','sdf','zsdf','Zsdf','Asdf','asdf']

module('charorder plugin')
test('non latin characters plugin', function() {

  ok((()=>{
    const aSorted = tinysort(zenLi('ul>li{a$}*'+aLangSR.length,{a:aLangSR}),{charOrder:'cd{d}l{lj}n{nj}sz'})
      ,sSorted = eachElement(aSorted,elm=>' '+elm.textContent)
    return sSorted==' coga ega ovjek dep godina gospodin liljana luak ljubav mukarac no njuka zec'
  })(),'tinysort(nodeList,{charOrder:\'cd{d}l{lj}n{nj}sz\'}); // Serbo-Croatian')

  ok((()=>{
    const aSorted = tinysort(zenLi('ul>li{a$}*'+aLangDA.length,{a:aLangDA}),{charOrder:'[{Aa}]'})
      ,sSorted = eachElement(aSorted,elm=>' '+elm.textContent)
    return sSorted==' Kbenhavn ble resund ben Aarhus se'
  })(),'tinysort(nodeList,{charOrder:\'[{Aa}]\'}); // Danisch')

  ok((()=>{
    const aSorted = tinysort(zenLi('ul>li{a$}*'+aLangFR.length,{a:aLangFR}),{charOrder:'a[]c[]e[]i[]o[]u[]'})
      ,sSorted = eachElement(aSorted,elm=>' '+elm.textContent)
    return sSorted===' Asdf asdf sdf sdf zsdf Zsdf'
  })(),'tst')

  //console.log('',eachElement(zenLi('ul>li{a$}*'+aLangDA.length,{a:aLangDA}),function(elm){ return ' '+elm.textContent; })); // log
  /*ok((()=>{
    const aSorted = tinysort(zenLi('ul>li{a$}*'+aLangDA.length,{a:aLangDA}),{charOrder:'[{Aa}]'})
      ,sSorted = eachElement(aSorted,function(elm){ return ' '+elm.textContent; });
    return sSorted==' Kbenhavn ble resund ben Aarhus se';
  })(),'tinysort(nodeList,{charOrder:\'a[]c[]e[]i[]o[]u[]\'}); // French');*/
})

